.btn {
 font-size: 1rem;
 display: inline-flex;
 align-items: center;
 justify-content: center;
 font-weight: 600;
 position: relative;
 outline: none;
 border: none;
 border-radius: 9999px;
 position: relative;
 cursor: pointer;
 width: fit-content;
 box-sizing: border-box;
 text-decoration: none;
 min-height: 2.25em;
 max-width: 17.5em;
 padding: 0 20px;
 color: #ffffff;

 &::before {
  position: absolute;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  left: -5px;
  top: -5px;
  display: block;
  content: "";
  border-radius: 9999px;
  border-color: transparent;
  transition: border-color 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  box-sizing: border-box;
 }

 &::after {
  position: absolute;
  content: "";
  display: block;
  height: 100%;
  border-radius: 9999px;
  left: 0;
  top: 0;
  right: calc(100% - 3em);
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1),
   opacity 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  opacity: 0;
  box-sizing: border-box;
 }

 &:hover::after {
  right: 0;
  opacity: 1;
 }

 &.btnprimary {
  background-color: #2ca01c;

  &::after {
   background-color: #0d7300;
  }
 }

 &.btnsecondary {
  background-color: #393a3d;

  &::after {
   background-color: #000000;
  }
 }
}

.btntext {
 z-index: 1;
 display: inline-block;
 position: relative;
}