.countdown {
 display: flex;
 align-items: center;
 height: 5rem;
 justify-content: center;
 margin-bottom: 20px;

 &.height {
  height: 5rem;
 }
}

.unit {
 background: #ffffff;
 border-radius: .25rem;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 color: #393a3d;
 margin-right: 0.5rem;

 height: 2.75rem;
 width: 2.5rem;

 @media only screen and (min-width: 1024px) {
  height: 4.125rem;
  width: 4rem;
 }
}

.number {
 font-size: 1.25rem;
 font-weight: 600;
 line-height: 1.3;
 padding-bottom: 1px;

 @media only screen and (min-width: 1024px) {
  font-size: 1.75rem;
 }
}

.length {
 font-weight: 600;
 font-size: 0.625rem;

 @media only screen and (min-width: 1024px) {
  font-size: 0.875rem;

 }
}