.formContainer {
 padding-top: 20px;
 box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
 border-radius: 20px;
 width: 100%;
 max-width: 1440px;
 margin: 20px auto;
 padding: 20px;
}

.heading,
 {
 font-size: 1.75rem;
 line-height: 2rem;
 font-weight: 600;
 line-height: 1;
 padding-bottom: 20px;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 2.25rem;
  line-height: 2.75rem;
 }
}

.success {
 margin-top: 80px;
 color: #2ca01c;
}

.subHeading,
.success {
 font-size: 1.25rem;
 line-height: 1;
 font-weight: 500;
 padding-bottom: 20px;
}

.form__item {
 position: relative;
 margin: 15px;


}

