.tableComp {
 max-width: 1080px;
 margin-left: auto;
 margin-right: auto;
 padding: 20px 0;
}

.table {
 font-family: arial, sans-serif;
 border-collapse: collapse;
 width: 100%;
 border-bottom: solid 6px #cfcfcf;

}

.th {
 padding: 16px;
}

.tbody {
 border: 1px solid #cfcfcf;
}

.td {
 padding: 16px;
 border-bottom: 1px solid #cfcfcf;
 border-right: 2px dotted #cfcfcf;
 font-size: 14px;
 line-height: 20px;
 color: #505050;
 vertical-align: top;

 &:not(:first-child) {
  text-align: center;

 }

 width:20%;

 &:first-child {
  width: 60%;
  border-left: 1px solid transparent;
 }
}

.tr {
 &:nth-child(even) {
  background: #f7f7f7;
 }
}

.icon {
 margin: 0 auto 10px auto;
 width: 30px;
 height: 20px;
 display: block;
}

.headerSection {
 display: table;
 margin-bottom: 20px;
 width: 100%;
}

.compTitle {
 font-size: 20px;
 padding-right: 10px;
 padding-left: 10px;
 font-weight: 300;
 text-align: left;
 vertical-align: bottom;

 @media only screen and (min-width: 426px) {
  font-size: 32px;
  padding-right: 20%;
  padding-bottom: 10px;
 }

}

.prodOne,
.prodTwo {
 font-size: 14px;
 text-align: center;
 color: #ffffff;
 padding: 40px 0;
 max-height: 100px;
 border: 10px solid #ffffff;

 @media only screen and (min-width: 426px) {
  font-size: 22px;
 }
}

.prodOne {
 background: #2CA01C;
}

.prodTwo {
 background: #8D9096;
}

hr {
 margin-top: 20px;
 margin-bottom: 20px;
 border: 0;
 border-top: 1px solid #eee;
}