.tooltipBtn {
 font-size: 14px;
 line-height: 1.5;
 font-weight: 400;
 letter-spacing: normal;
 padding: 0;
 margin: 0;
 border: none;
 display: flex;
 align-items: center;
 cursor: pointer;
 outline: none;
 background: none;
 color: #393a3d;


}

.after {
 text-decoration: underline;

 &::after {
  content: "i";
  display: flex;
  border: solid 0.0625rem #000000;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  justify-content: center;
  align-items: center;
  font-size: 0.5em;
  margin-left: 4px;
 }
}

.before {
 text-decoration: none;

 &::before {
  content: "i";
  display: flex;
  border: solid 0.0625rem #000000;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  justify-content: center;
  align-items: center;
  font-size: 0.5em;
  margin-right: 4px;
 }
}

.tooltip {
 position: relative;
 display: inline-flex;
 user-select: none;
 cursor: pointer;
 margin: 0 10px;
 -webkit-tap-highlight-color: transparent;
 color: #fff;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
}

.tooltipConent {
 margin: 0 10px;
 position: absolute;
 padding: 12px;
 font-size: 12px;
 cursor: default;
 border-radius: 4px;
 /* white-space: nowrap; */
 min-width: 190px;
 background-color: #393a3d;
 animation: fadeIn ease-in-out 0.25s;
 color: #fff;
 z-index: 1
}


.arrow {
 position: absolute;
 width: 0;
 height: 0;
}

.top {
 bottom: calc(100% + 9px);
 left: 50%;
 transform: translateX(-50%);

 .arrow {
  bottom: -10px;
  left: calc(50% - 10px);
  border-right: 10px solid transparent;
  border-top: 10px solid #393a3d;
  border-left: 10px solid transparent;
 }
}

.right,
.left {
 top: 50%;
 transform: translateY(-50%);

 .arrow {
  top: calc(50% - 10px);
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
 }
}

.right {
 left: calc(100% + 18px);

 .arrow {
  left: -10px;
  border-right: 10px solid #393a3d;
 }
}

.left {
 right: calc(100% + 18px);

 .arrow {
  right: -10px;
  border-left: 10px solid #393a3d;
 }
}

.bottom {
 top: calc(100% + 9px);
 left: 50%;
 transform: translateX(-50%);

 .arrow {
  top: -10px;
  left: calc(50% - 10px);
  border-right: 10px solid transparent;
  border-bottom: 10px solid #393a3d;
  border-left: 10px solid transparent;
 }
}

@keyframes fadeIn {
 0% {
  opacity: 0;
 }

 100% {
  opacity: 1;
 }
}