//Modal

.modalContent {
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
 position: fixed;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 background: #fff;
 padding: 50px;
 z-index: 1000;
 border-radius: 8px;
 padding: 32px;


 &.teal {
  background-color: #00c1bf;
 }

 @media only screen and (max-width: 768px) {
  width: 75%;
 }
}

.modal {
 position: fixed;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 background-color: rgba(0, 0, 0, .4);
 z-index: 1000
}

.closeButton {
 position: absolute;
 top: 5px;
 right: 10px;

 &:hover {
  cursor: pointer;
 }
}

.promoContent {
 max-width: 400px;
 text-align: center;
 margin: 28px;
}

.promoSubtitle {
 font-size: 1.75rem;
 line-height: 2rem;
 letter-spacing: normal;
 font-weight: 500;
 margin-bottom: 20px;

 @media only screen and (max-width: 768px) {
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 600;
 }
}

.promoTitle {
 font-size: 4rem;
 line-height: 4rem;
 font-weight: 700;
 letter-spacing: normal;
 margin-bottom: 20px;

 sup {
  font-size: 2rem;
  font-weight: 400;
 }

 @media only screen and (max-width: 768px) {
  font-size: 2.25rem;
  line-height: 2.75rem;
  font-weight: 600;
 }
}