// Welcome Screen Starts

.welcomeScreen {
 display: flex;
 flex-direction: column;
 align-items: center;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
 width: 100%;
 padding: 5rem 2rem;
 text-align: center;

 @media only screen and (min-width: 768px) {
  padding-left: 2rem;
  padding-right: 2rem;
 }
}

.welcomeTitle {
 font-size: 1.75rem;
 line-height: 2rem;
 font-weight: 600;
 letter-spacing: normal;
 margin-top: 0;
 margin-bottom: 20px;
}

.welcomeSubtitle {
 line-height: 1.5rem;
 font-weight: 400;
 letter-spacing: normal;
}

.welcomeImages {
 flex-flow: row wrap;
 padding-left: 2rem;
 display: flex;
 justify-content: center;
 width: 100%;
 text-align: center;
 padding-top: 40px;

 @media only screen and (min-width: 768px) {
  flex-flow: row nowrap;
 }
}

.welcomeImageItem {
 margin-top: -2rem;
 height: 8.5rem;

 &:nth-child(even) {
  margin-left: -3rem;
 }

 @media only screen and (min-width: 1024px) {
  margin-left: -3rem;
  height: 12.5rem;
 }
}

// Welcome Screen Ends

//Quiz Question Starts
.questionScreen {
 padding: 20px;
 margin: 40px;
 border-radius: .25rem;
 box-shadow: 0 0.125rem 0.375rem 0 rgba(0, 0, 0, 0.1);
 border: .0625rem solid #eceef1;
 background-color: #fff;
 max-width: 58.75rem;
 width: 100%;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
 display: flex;
 flex-direction: column;
 text-align: center;
 position: relative;



 @media only screen and (min-width: 768px) {
  width: 95%;
 }

 @media only screen and (min-width: 1024px) {}



}

.questionScreenQuestion {
 font-size: 1.75rem;
 line-height: 2rem;
 font-weight: 600;
 letter-spacing: normal;
 margin-bottom: 12px;
}

.questionScreenSubQuestion {
 font-size: 1.25rem;
 line-height: 1.75rem;
 font-weight: 400;
 letter-spacing: normal;
 margin-bottom: 60px;
}

.questionScreenOptions {
 display: flex;
 flex-direction: column;
 justify-content: space-around;

 @media only screen and (min-width: 768px) {
  flex-direction: row;

 }
}

.questionScreenOption {
 margin-bottom: 20px;
 display: flex;
 flex-direction: row;
 align-items: flex-start;
 width: 100%;
 text-align: left;

 &:hover {
  cursor: pointer;
 }

 &:hover,
 &:active {
  .optionImage {
   box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
   transition: opacity 0.5s ease-in-out;
  }
 }

 @media only screen and (min-width: 768px) {
  max-width: 15rem;
  flex-direction: column;
  text-align: center;
  align-items: center;
 }
}

.optionImage {
 width: 5rem;
 height: 5rem;
 margin-right: 20px;

 @media only screen and (min-width: 768px) {
  width: 11.25rem;
  height: 11.25rem;
  border-radius: 50%;
  margin-bottom: 20px;
 }
}

.optionContents {
 display: flex;
 flex-direction: column;
}

.optionValue {
 font-size: 1.5rem;
 line-height: 1.75rem;
 font-weight: 600;
 letter-spacing: normal;
}

.optionText {
 font-size: 1.25rem;
 line-height: 1.75rem;
 font-weight: 400;
 letter-spacing: normal;
}

.optionActive {
 border: solid 0.1875rem #2ca01c;

}

.questionScreenBackbutton {
 cursor: pointer;
 background: #ffffff;
 border-radius: 50%;
 border: solid 0.0625rem #e3e5e8;
 width: 2.5rem;
 height: 2.5rem;
 display: flex;
 justify-content: center;
 align-items: center;
 position: absolute;
 top: -1.5rem;
 left: 0;

 @media only screen and (min-width: 768px) {
  top: 40%;
  left: -1rem;
 }

 @media only screen and (min-width: 1024px) {
  left: -3.5rem;
 }

 &:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  transition: opacity 0.5s ease-in-out;
 }

 &:active,
 &:focus {
  outline: 1;
  border: 1
 }
}

// Quiz Question Ends

// Completed Screen Start

.productAnswerMessage {
 display: flex;
 margin: 20px;
 flex-direction: column;

 @media only screen and (min-width: 768px) {
  flex-direction: row;
  max-width: 1024px;
  margin: 40px auto;
 }
}

.productAnswerImage {
 display: none;

 @media only screen and (min-width: 768px) {
  display: block;
  margin: 0 auto;
 }

}

.productAnswerContent {
 @media only screen and (min-width: 768px) {
  margin-left: 80px;
 }
}

// ******************* Start styling from here

.productAnswerTitle {
 font-size: 2.25rem;
 line-height: 2.75rem;
 font-weight: 600;
 letter-spacing: normal;
 padding-bottom: 40px;
}

.productAnswerRecommended {
 color: #108000;
}

.productAnswerText {
 font-size: 1.25rem;
 line-height: 1.75rem;
 font-weight: 400;
 letter-spacing: normal;
}

//Main Quiz Starts

.productSelectorQuiz {
 display: flex;
 flex-direction: column;
 align-items: center;

}

.dots {
 position: absolute;
 left: 0;
 display: flex;
 justify-content: center;
 width: 100%;
}

.dot {
 width: 1rem;
 height: 1rem;
 background: #eceef1;
 border-radius: 50%;
 margin: 0 10px;
}

.activeDot {
 background: #2ca01c;
}

//Main Quiz Ends

//Cards External

.showRecommended {
 display: block;
}

.hideRecommended {
 display: none;
}