.CaEnterpriseAddonCalculator {
 padding: 20px;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
}

.users {
 display: flex;
 justify-content: center;
}

.radio {
 position: relative;
 display: flex;
 align-items: center;
 line-height: 1;
 margin-right: 8px;
 margin-bottom: 40px;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  margin-right: 40px;
 }
}

.radiolabel {
 cursor: pointer;
 padding-left: 8px;
}

.radioinput {
 position: absolute;
 width: 20px;
 height: 20px;
 opacity: 0;
 cursor: pointer;

 &:checked {
  &~.radiocircle {
   &:after {
    content: "";
    display: block;
    height: 12px;
    width: 12px;
    background: #2ca01c;
    border-radius: 50%;
   }
  }
 }
}

.radiocircle {
 cursor: pointer;
 width: 20px;
 height: 20px;
 border-radius: 50%;
 border: solid 1px #8d9096;
 background: #ffffff;
 display: flex;
 justify-content: center;
 align-items: center;
}

.chart {

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
  flex-wrap: nowrap;

 }
}

.chartproducts {

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  background: #ffffff;
  border: solid 1px #e3e5e8;
  padding: 20px;
  border-radius: 4px;
  display: flex;
  margin-bottom: 20px;
  max-width: 52.6875rem;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
  margin-bottom: 0;
 }
}

// Individual Price Cards
.chartitem {
 background: #ffffff;
 width: 100%;
 padding: 20px;
 ;
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 margin-bottom: 8px;
 border: solid 1px #f4f5f8;
 border-radius: 4px;
 position: relative;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  max-width: 280px;
  padding: 0 20px;
  border: none;
  border-right: solid 1px #f4f5f8;
  border-radius: none;
  margin-bottom: 20px;

  &:last-child {
   padding: 0 0 0 20px;
   border-right: none;
  }

  &:first-child {
   padding: 0 20px 0 0;
  }
 }
}

.chartitemtop {
 margin-bottom: 20px;
 padding-bottom: 20px;
 display: flex;
 position: relative;
 width: 100%;

 &:after {
  content: "";
  width: 40px;
  height: 2px;
  border-radius: 2px;
  background: #2ca01c;
  position: absolute;
  bottom: 0;
  left: 0;
 }
}

.chartitemtitle {
 font-size: 1.25rem;
 line-height: 1.5rem;
 font-weight: 600;
 letter-spacing: normal;
 width: 100%;
 flex: 1 1 156px;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 1.5rem;
  line-height: 1.75rem;
  letter-spacing: normal;
  height: 56px;
 }
}

.charttoggle {
 position: relative;
}

.charttogglelabel {
 width: 52px;
 height: 32px;
 background: #6b6c72;
 border-radius: 32px;
 position: relative;
 border: none;
 cursor: pointer;
 display: block;

 &:after {
  position: absolute;
  content: "";
  display: block;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  background: #ffffff;
  left: 4px;
  top: 4px;
  transition: left 0.2s cubic-bezier(0.35, 0, 0.25, 1);
 }
}

.charttoggleinput {
 position: absolute;
 width: 52px;
 height: 32px;
 opacity: 0;

 &:checked~.charttogglelabel {
  background: #2ca01c;

  &:after {
   left: calc(100% - 3px - 24px)
  }
 }
}

.chartpriceadd {
 font-size: 0.875rem;
 line-height: 1.25rem;
 letter-spacing: normal;
 font-weight: 500;
 margin-bottom: 4px
}

.chartprice {
 display: flex;
 align-items: flex-start;
 line-height: 1;
 margin-bottom: 20px;
 padding-bottom: 20px;
 border-bottom: solid 1px #f4f5f8;
}

.chartpricedollars {
 font-size: 1.75rem;
 line-height: 2rem;
 font-weight: 600;
 line-height: 1;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 2.25rem;
  line-height: 2.75rem;
 }
}

.chartpricecents {
 font-size: 1rem;
 margin-top: 4px;
 line-height: 1;
 font-weight: 500;
}

.chartpriceterm {
 font-size: 1rem;
 font-weight: 500;
 line-height: 1;
 align-self: flex-end;
 margin-bottom: 4px
}

.chartfeatures {
 list-style-type: none;
 margin: 0 0 20px 0;
 padding: 0;
}

.chartfeaturesitem {
 margin-bottom: 8px;
 font-size: 0.875rem;
 line-height: 1.25rem;
 font-weight: 400;
 letter-spacing: normal;
 display: flex;
}

.total {
 background: #ffffff;
 border: solid 1px #e3e5e8;
 padding: 20px;
 border-radius: 4px;
 width: 100%;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  display: flex;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
