 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  border-right: solid 1px #f4f5f8;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
  border-right: none;
 }
}

.totaltitle {
 font-size: 1.25rem;
 line-height: 1.5rem;
 font-weight: 600;
 letter-spacing: normal;
 padding-bottom: 8px;
 margin-bottom: 40px;
 position: relative;

 &:after {
  content: "";
  width: 40px;
  height: 2px;
  border-radius: 2px;
  background: #2ca01c;
  position: absolute;
  bottom: 0;
  left: 0;
 }
}

.totalprice {
 display: flex;
 align-items: flex-start;
 line-height: 1;
}

.totalpricedollars {
 font-size: 2.25rem;
 line-height: 2.75rem;
 letter-spacing: normal;
 font-weight: 600;
 line-height: 1;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 2.75rem;
 }
}

.totalpricecents {
 font-size: 1.25rem;
 font-weight: 600;
 letter-spacing: normal;
 margin-top: 4px;
 line-height: 1;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 1.5rem;
 }
}

.totalpriceterm {
 font-size: 1.25rem;
 font-weight: 600;
 line-height: 1;
 align-self: flex-end;
 margin-bottom: 4px;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  font-size: 1.5rem;
 }
}

.totalpriceinfo {
 font-size: 0.875rem;
 line-height: 1.25rem;
 letter-spacing: normal;
 font-weight: 500;
 margin: 20px 0;
 border-bottom: solid 1px #f4f5f8;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  border-bottom: none;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
  border-right: none;
  padding-bottom: 20px;
  border-bottom: solid 1px #f4f5f8;
 }
}

.includes {
 flex: 0 0 50%;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  padding-left: 20px;
 }

 // DESKTOP STYLES
 @media only screen and (min-width: 1024px) {
  padding-left: 0px;
 }
}

.includestitle {
 font-size: 0.875rem;
 line-height: 1.25rem;
 margin-bottom: 20px;
 font-weight: 500;
}

.includesitem {
 font-size: 0.875rem;
 line-height: 1.25rem;
 font-weight: 400;
 margin: 0 20px 20px 0;
 display: flex;
 justify-content: space-between;

 // TABLET STYLES
 @media only screen and (min-width: 768px) {
  margin-bottom: 20px;
 }
}

.includescost {
 font-weight: 500;
}

.checkmark {
 width: 14px;
 height: 14px;
 flex: 0 0 14px;
 right: 0;
 margin-right: 8px;
 top: 0;

 &::before {
  height: 100%;
 }

 &::after {
  width: 60%;
 }
}

.btn {
 font-size: 1rem;
 display: inline-flex;
 align-items: center;
 justify-content: center;
 font-weight: 600;
 position: relative;
 outline: none;
 border: none;
 border-radius: 9999px;
 position: relative;
 cursor: pointer;
 width: fit-content;
 box-sizing: border-box;
 text-decoration: none;
 min-height: 2.25em;
 max-width: 17.5em;
 padding: 0 20px;
 color: #ffffff;

 &::before {
  position: absolute;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  left: -5px;
  top: -5px;
  display: block;
  content: "";
  border-radius: 9999px;
  border-color: transparent;
  transition: border-color 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  box-sizing: border-box;
 }

 &::after {
  position: absolute;
  content: "";
  display: block;
  height: 100%;
  border-radius: 9999px;
  left: 0;
  top: 0;
  right: calc(100% - 3em);
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1),
   opacity 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  opacity: 0;
  box-sizing: border-box;
 }

 &:hover::after {
  right: 0;
  opacity: 1;
 }

 &.btnprimary {
  background-color: #2ca01c;

  &::after {
   background-color: #0d7300;
  }
 }

 &.btnsecondary {
  background-color: #393a3d;

  &::after {
   background-color: #000000;
  }
 }
}

.chartbtn {
 width: auto;
 margin-top: auto;

 // DESKTOP STYLES
 @media only screen and (min-width: 768px) {
  position: absolute;
  bottom: -1.5rem;
 }
}

.btntext {
 z-index: 1;
 display: inline-block;
 position: relative;
}