.quickbookspayrollchart {
 display: flex;
 margin: 40px auto;
 padding: 20 6.667%;
 max-width: 1040px;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;

 @media only screen and (min-width: 768px) {
  justify-content: center;
 }
}

.pricingcolumn {
 width: 100%;
 flex: 0 0 100%;
 display: flex;
 flex-direction: column;
 border-radius: .25rem;
 justify-content: center;
 box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
 background: #ffffff;
 transition: transform 0.3s cubic-bezier(0.35, 0, 0.5, 1);

 @media only screen and (min-width: 768px) {
  width: 83.333%;
  flex: 0 0 75%;
  flex-direction: row;
 }

 @media only screen and (min-width: 1024px) {
  flex-direction: row;
  width: 83.333%;
  flex: 0 0 63.333%;
 }
}

.head {
 padding: 20px 8px 20px 20px;
 display: flex;
 flex-direction: column;
}

.headtitle {
 font-size: 20px;
 line-height: 1.5;
 font-weight: 600;
 letter-spacing: normal;
 margin-bottom: 20px;
 position: relative;

 @media only screen and (min-width: 768px) {
  font-size: 24px;
 }

 &:before {
  content: "";
  display: block;
  position: absolute;
  top: calc(100% + 8px);
  width: 2.5rem;
  height: 0.125rem;
  border-radius: 0.0625rem;
  background: #2ca01c;
 }
}

.headpricing {
 font-size: 28px;
 line-height: 1.3;
 font-weight: 600;
 letter-spacing: normal;
 margin-top: 20px;

 @media only screen and (min-width: 768px) {
  margin-top: 20px;
  font-size: 34px;
 }

 & small {
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: normal;
 }
}

.headcta {
 border-bottom: .0625rem solid #d4d7dc;
 padding-bottom: 20px;

 @media only screen and (min-width: 768px) {
  border-bottom: none;
  padding-bottom: auto;
 }
}

.headduration {
 font-size: 20px;
 line-height: 1.5;
 font-weight: 600;
 letter-spacing: normal;
 padding-bottom: 20px;

 @media only screen and (min-width: 768px) {
  font-size: 24px;
 }
}

.columnmainlist {
 padding: 0 20px 20px;
 margin: 0;
 list-style-type: none;

 & p {
  margin-bottom: 8px;
  font-weight: 600;
  margin-top: auto;
 }

 @media only screen and (min-width: 768px) {
  padding: 80px 20px 20px;
 }
}

.mainfeature {
 margin-bottom: 0;
 position: relative;
}

.mainfeaturetitle {
 font-size: 14px;
 line-height: 1.5;
 font-weight: 400;
 letter-spacing: normal;
 padding: 0;
 margin: 0;
 border: none;
 display: flex;
 align-items: center;
 cursor: pointer;
 outline: none;

 &::before {
  content: "i";
  display: flex;
  border: solid 0.0625rem #000000;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  justify-content: center;
  align-items: center;
  font-size: 0.5em;
  margin-right: 4px;
 }

}

.tooltip {
 position: relative;
 display: inline-block;
}

.tooltiptext {
 top: -.75rem;
 transform: translateY(-100%);
 background: #393a3d;
 position: absolute;
 border: .0625rem solid #8d9096;
 color: #fff;
 font-weight: 500;
 font-size: .75rem;
 width: 11.25rem;
 width: max-content;
 min-width: 5rem;
 max-width: 11.25rem;
 border-radius: .25rem;
 z-index: 1500;
 padding: 1rem;
 line-height: 1.3;
 animation: show .2s cubic-bezier(.35, 0, .25, 1) forwards;

 &::after {
  content: "";
  display: block;
  height: .75rem;
  width: .75rem;
  border-left: 0;
  border-top: 0;
  bottom: 0;
  transform: translateY(55%) rotate(45deg);
  background: #393a3d;
  position: absolute;
 }
}

@keyframes show {
 from {
  opacity: 0;
 }

 to {
  opacity: 1;
 }
}