.simpleProductCompare {
 width: 100%;
 max-width: 1080px;
 position: relative;
 display: flex;
 flex-direction: column;
 margin: 20px auto 0 auto;
 font-family: AvenirNext forINTUIT, Helvetica, Arial, sans-serif;
 color: #585858;
}

.shadow__wrapper {
 display: none;
 width: 100%;
 height: 100%;
 position: absolute;
 overflow: visible;
 pointer-events: none;
 background: none;

 @media only screen and (min-width: 1024px) {
  display: flex;
 }
}

.shadow__cell {
 position: relative;
 flex: 1 1 100%;
 box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
 width: 300px;
 height: 100%;
 top: 0;

 background: transparent;

 @media only screen and (min-width: 1024px) {
  margin-left: 8px;
  margin-right: 8px;
 }

 &:first-child {
  display: none;

  @media only screen and (min-width: 1024px) {
   display: block;
   box-shadow: none;
   margin-left: 0;
   margin-right: -8px;
  }
 }
}

.headings__wrapper {
 width: 100%;
}


.headings__row {
 display: flex;
 flex-direction: row;
}

.headings__cell {
 position: relative;
 flex: 1 1 100%;
 padding-top: 20px;
 padding-right: 40px;
 padding-bottom: 20px;
 padding-left: 8px;
 background-color: #ffffff;
 font-size: 1rem;
 font-weight: 600;
 text-align: left;
 display: flex;
 flex-direction: column;

 &::after {
  content: "";
  position: absolute;
  top: 34%;
  right: 0;
  width: 0.0625rem;
  height: 50%;
  background-color: #e3e5e8;

  @media only screen and (min-width: 1024px) {
   width: 0px;
  }
 }

 @media only screen and (min-width: 1024px) {
  box-shadow: none;
  margin-left: 8px;
  margin-right: 8px;
  border-top: solid 0.0625rem #e3e5e8;
  border-right: solid 0.0625rem #e3e5e8;
  border-left: solid 0.0625rem #e3e5e8;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  padding-top: 20px;
  padding-right: 40px;
  padding-bottom: 20px;
  padding-left: 40px;
  font-size: 1.5rem;
 }

 &:first-child {
  display: none;
  border: none;
  background-color: transparent;

  .productTitle {
   position: inherit;


   &::after {
    width: 0px;
   }
  }

  @media only screen and (min-width: 1024px) {
   display: block;
   margin-left: 0;
   margin-right: -8px;
  }
 }

 &:nth-child(2) {
  padding-left: 0;

  @media only screen and (min-width: 1024px) {
   padding-left: 40px;
  }
 }

 &:last-child {
  &::after {
   width: 0px;
  }
 }
}

.headings__cell .headings__cell:last-child {
 &::after {
  width: 0px;
 }
}

.productTitle {
 position: relative;
 margin-bottom: 20px;

 &::after {
  position: absolute;
  content: "";
  height: 0.125rem;
  background-color: #53b700;
  left: 0;
  bottom: -8px;
  width: 1.5rem;

  @media only screen and (min-width: 1024px) {
   width: 2.5rem;
  }
 }
}

.shadow__line {
 width: 100vw;
 height: 0.75rem;
 position: relative;
 left: 49%;
 right: 50%;
 margin-left: -50vw;
 margin-right: -50vw;
 overflow: hidden;
 box-shadow: 0px 6px 0.25rem -3px rgba(0, 0, 0, 0.1);
 z-index: 1;

 @media only screen and (min-width: 1024px) {
  display: none;
 }
}

.rows__wrapper {
 width: 100%;
 padding-bottom: 40px;

 @media only screen and (min-width: 1024px) {
  padding: 0;
 }
}

.rows__row {
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;

 @media only screen and (min-width: 1024px) {
  flex-wrap: nowrap;

  &:last-child {
   .rows__cell {
    &:nth-last-child(-n + 2) {
     border-bottom: solid 0.0625rem #e3e5e8;
     border-right: solid 0.0625rem #e3e5e8;
     border-left: solid 0.0625rem #e3e5e8;
     border-bottom-left-radius: 0.25rem;
     border-bottom-right-radius: 0.25rem;
    }
   }
  }
 }
}

.rows__cell {
 width: 50%;
 border-top: 0.0625rem solid #e3e5e8;
 border-right: 0.0625rem solid #e3e5e8;
 border-bottom: 0.0625rem solid #e3e5e8;
 border-left: 0.0625rem solid #e3e5e8;
 padding: 8px;
 background-color: #ffffff;
 text-align: center;
 font-size: 0.75rem;
 font-weight: 400;

 @media only screen and (min-width: 1024px) {
  width: 100%;
  margin-left: 8px;
  margin-right: 8px;
  padding-top: 16px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
  border-bottom: none;
  font-size: 0.875rem;
  font-weight: 400;
 }

 sup {
  top: -0.5rem;
 }

 &:first-child {
  width: 100%;
  margin-left: 0;
  margin-right: -8px;
  border-top: none;
  border-right: none;
  border-bottom: none;
  border-left: none;
  box-shadow: none;
  padding-top: 20px;
  padding-left: 0;
  background-color: transparent;
  text-align: left;
  font-size: 0.875rem;
  font-weight: 500;

  @media only screen and (min-width: 1024px) {
   border-top: 0.0625rem solid #e3e5e8;
   padding-top: 16px;
   padding-right: 20px;
   padding-bottom: 16px;
   padding-left: 20px;
   font-size: 1rem;
   font-weight: 500;
  }
 }

 &:nth-child(2) {
  border-right: none;

  @media only screen and (min-width: 1024px) {
   border-right: 0.0625rem solid #e3e5e8;
  }
 }
}



.icon {
 margin: 0 auto 10px auto;
 width: 30px;
 height: 20px;
 display: block;
}

.toggle__button {
 padding: 20px;
 display: flex;
 align-items: center;
 justify-content: center;
 margin: 0 auto;
}